<template>
  <div>
    <div class="shopcart">
      <div class="content" @click="toggleList">
        <div class="content-left">
          <div class="logo-wrapper">
            <div class="logo" :class="{'highlight':totalCount>0}">
              <i class="icon-shopping_cart" :class="{'highlight':totalCount>0}"></i>
            </div>
            <div class="num" v-show="totalCount>0">{{totalCount}}</div>
          </div>
          <div class="price" :class="{'highlight':totalPrice>0}">￥{{totalPrice}}</div>
          <div class="desc">另需配送费￥{{deliveryPrice}}元</div>
        </div>
        <div class="content-right" @click.stop.prevent="pay">
          <div class="pay" :class="payClass">
            {{payDesc}}
          </div>
        </div>
      </div>
      <div class="ball-container">
        <div v-for="ball in balls">
          <transition name="drop" @before-enter="beforeEnter" @enter="enter" @after-enter="afterEnter">
            <div class="ball" v-show="ball.show">
              <div class="inner inner-hook"></div>
            </div>
          </transition>
        </div>
      </div>
      <transition name="fold">
        <div class="shopcart-list" v-show="listShow">
          <div class="list-header">
            <h1 class="title">购物车</h1>
            <span class="empty" @click="empty">清空</span>
          </div>
          <div class="list-content" ref="listContent">
            <ul>
              <li class="food" v-for="food in selectFoods">
                <span class="name">{{food.name}}</span>
                <div class="price">
                  <span>￥{{food.price*food.count}}</span>
                </div>
                <div class="cartcontral-wrapper">
                  <cartcontral @add="addFood" :food="food"></cartcontral>
                </div>
              </li>
            </ul>
          </div>
        </div>
      </transition>
    </div>
    <transition name="fade">
      <div class="list-mask" @click="hideList" v-show="listShow"></div>
    </transition>
  </div>
</template>

<script type="ecmascript-6">
  import BScroll from 'better-scroll';
  import cartcontral from 'components/cartcontral/cartcontral';

  export default {
    props: {
      selectFoods: {
        type: Array,
        default() {
          return [
          {
            price: 10,
            count: 1
          }
          ];
        }
      },
      deliveryPrice: {
        type: Number,
        default: 0
      },
      minPrice: {
        type: Number,
        default: 0
      }
    },
    data() {
      return {
        balls: [
        {
          show: false
        },
        {
          show: false
        },
        {
          show: false
        },
        {
          show: false
        },
        {
          show: false
        }
        ],
        dropBalls: [],
        fold: true
      };
    },
    computed: {
      totalPrice() {
        let total = 0;
        this.selectFoods.forEach((food) => {
          total += food.price * food.count;
        });
        return total;
      },
      totalCount() {
        let count = 0;
        this.selectFoods.forEach((food) => {
          count += food.count;
        });
        return count;
      },
      payDesc() {
        if (this.totalPrice === 0) {
          return `￥${this.minPrice}元起送`;
        } else if (this.totalPrice < this.minPrice) {
          let diff = this.minPrice - this.totalPrice;
          return `还差￥${diff}元起送`;
        } else {
          return '去结算';
        }
      },
      payClass() {
        if (this.totalPrice < this.minPrice) {
          return 'not-enough';
        } else {
          return 'enough';
        }
      },
      listShow() {
        if (!this.totalCount) {
          this.fold = true;
          return false;
        }
        let show = !this.fold;
        if (show) {
          this.$nextTick(() => {
            if (!this.scroll) {
              this.scroll = new BScroll(this.$refs.listContent, {
                click: true
              });
            } else {
              this.scroll.refresh();
            }
          });
        }
        return show;
      }
    },
    methods: {
      drop(el) {
        /*console.log(el);*/
        for (let i = 0; i < this.balls.length; i++) {
          let ball = this.balls[i];
          if (!ball.show) {
            ball.show = true;
            ball.el = el;
            this.dropBalls.push(ball);
            return;
          }
        }
      },
      toggleList() {
        if (!this.totalCount) {
          return;
        }
        this.fold = !this.fold;
      },
      hideList() {
        this.fold = true;
      },
      empty() {
        this.selectFoods.forEach((food) => {
          food.count = 0;
        });
      },
      pay() {
        if (this.totalPrice < this.minPrice) {
          return;
        }
        window.alert(`支付${this.totalPrice}元`);
      },
      addFood(target) {
        this.drop(target);
      },
      beforeEnter(el) {
        let count = this.balls.length;
        while (count--) {
          let ball = this.balls[count];
          if (ball.show) {
            let rect = ball.el.getBoundingClientRect();
            let x = (rect.left - 32);
            let y = -(window.innerHeight - rect.top - 22);
            el.style.display = '';
            el.style.webkitTransform = `translate3d(0,${y}px,0)`;
            el.style.transform = `translate3d(0,${y}px,0)`;
            let inner = el.getElementsByClassName('inner-hook')[0];
            inner.style.webkitTransform = `translate3d(${x}px,0,0)`;
            inner.style.transform = `translate3d(${x}px,0,0)`;
          }
        }
      },
      enter(el, done) {
        /* eslint-disable no-unused-vars */
        let rf = el.offsetHeight;
        this.$nextTick(() => {
          el.style.webkitTransform = 'translate3d(0,0,0)';
          el.style.transform = 'translate3d(0,0,0)';
          let inner = el.getElementsByClassName('inner-hook')[0];
          inner.style.webkitTransform = 'translate3d(0,0,0)';
          inner.style.transform = 'translate3d(0,0,0)';
          el.addEventListener('transitionend', done);
        });
      },
      afterEnter(el) {
        let ball = this.dropBalls.shift();
        if (ball) {
          ball.show = false;
          el.style.display = 'none';
        }
      }
    },
    components: {
      cartcontral
    }
  }
</script>

<style rel="stylesheet" lang="less" scoped>
  @import "../../common/styles/mixin.less"; 


  .shopcart{
    position: fixed;
    left: 0;
    bottom: 0;
    z-index: 50;
    width: 100%;
    height: 96/@r;
    .content{
      display: flex;
      background: #141d27;
      color:rgba(255, 255, 255,0.4);
      .content-left{
        flex:1;
        font-size:0;
        .logo-wrapper,
        .price,
        .desc{
          display: inline-block;
          vertical-align: top;
        }
        .logo-wrapper{
          position: relative;
          top:-20/@r;
          padding:12/@r;
          margin: 0 24/@r;
          width: 112/@r;
          height: 112/@r;
          box-sizing: border-box;   
          border-radius: 50%;
          background: #141d27;
          .logo{
            width: 100%;
            height: 100%;
            border-radius: 50%;
            text-align: center;
            background: #2b343c;
            &.highlight{
              background: rgb(0, 160, 220);
            }
            .icon-shopping_cart{
              line-height: 88/@r;
              font-size: 48/@r;
              color:#80858a;
              &.highlight{
                color: #fff;
              }
            }
          }
          .num{
            position: absolute;
            top: 0;
            right: 0;
            width: 48/@r;
            height: 32/@r;
            line-height: 32/@r;
            text-align: center;
            border-radius: 32/@r;
            font-weight: 700;
            font-size:18/@r;
            color: #fff;
            background: rgb(240, 20, 20);
            box-shadow: 0 4/@r 8/@r 0 rgba(0, 0, 0, 0.4);
          }
        }
        .price{
          line-height: 48/@r;
          margin-top: 24/@r;
          box-sizing: border-box;
          padding-right:24/@r;
          border-right: 2/@r solid rgba(255, 255, 255, 0.1);
          font-size:32/@r;
          font-weight: 700;
          &.highlight{
            color: #fff;
          }
        }
        .desc{
          margin: 24/@r 0 0 24/@r;
          line-height: 48/@r;
          font-size: 20/@r;
        }
      }
      .content-right{
        flex:0 0 210/@r;
        width: 210/@r;
        .pay{
          height: 100%;
          line-height: 96/@r;
          text-align: center;
          font-size: 24/@r;
          font-weight: 700;
          &.not-enough{
            background: #2b333b;
          }
          &.enough{
            background: #00b43c;
            color: #fff;
          }
        }
      }
    }
    .ball-container{
      .ball{
        position: fixed;
        left:64/@r;
        bottom: 44/@r;
        z-index: 200;
        transition: all 0.4s cubic-bezier(0.49, -0.29, 0.75, 0.41);
        .inner{
          width: 32/@r;
          height: 32/@r;
          border-radius: 50%;
          background: rgb(0, 160, 220);
          transition:all 0.4s linear;
        }
      }
    }
    .shopcart-list{
      position: absolute;
      left: 0;
      top: 0;
      z-index: -1;
      width: 100%;
      transform: translate3d(0, -100%, 0);
      &.fold-enter-active,
      &.fold-leave-active{
        transition: all 0.5s;
      }
      &.fold-enter,
      &.fold-leave-active{
        transform: translate3d(0, 0, 0);
      }
      .list-header{
        height: 80/@r;
        line-height: 80/@r;
        padding: 0 36/@r;
        background: #f3f5f7;
        border-bottom: 2/@r solid rgba(7, 17, 27, 0.1);
        .title{
          float: left;
          font-size: 28/@r;
          color: rgb(7, 17, 27);
        }
        .empty{
          float: right;
          font-size: 24/@r;
          color: rgb(0, 160, 220);
        }
      }
      .list-content{
        padding: 0 36/@r;
        max-height: 434/@r;
        overflow: hidden;
        background: #fff;
        .food{
          position: relative;
          padding: 28/@r 0;
          box-sizing: border-box;
          .border-1px(rgba(7, 17, 27, 0.1));
        }
        .name{
          line-height: 48/@r;
          font-size: 28/@r;
          color: rgb(7, 17, 27);
        }
        .price{
          position: absolute;
          right: 180/@r;
          bottom: 24/@r;
          line-height: 48/@r;
          font-size: 28/@r;
          font-weight: 700;
          color: rgb(240, 20, 20);
        }
        .cartcontral-wrapper{
          position: absolute;
          right: 0;
          bottom: 12/@r;
        }
      }
    }
  } 
  .list-mask{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 40;
    backdrop-filter: blur(10px);
    opacity: 1;
    background: rgba(7, 17, 27, 0.6);
    &.fade-enter-active,
    &.fade-leave-active{
      transition: all 0.5s;
    }
    &.fade-enter,
    &.fade-leave-active{
      opacity: 0;
      background: rgba(7, 17, 27, 0);
    }
  }
</style>


