<template>
  <div class="cartcontrol">
    <transition name="move">
      <div class="cart-decrease" v-show="food.count>0" @click.stop.prevent="decreaseCart">
        <span class="inner icon-remove_circle_outline"></span>
      </div>
    </transition>
    <div class="cart-count" v-show="food.count>0">{{food.count}}</div>
    <div class="cart-add icon-add_circle" @click.stop.prevent="addCart"></div>
  </div>
</template>

<script type="ecmascript-6">
  import Vue from 'vue';

  export default {
    props: {
      food: {
        type: Object
      }
    },
    methods: {
      addCart(event) {
        if (!event._constructed) {
          return;
        }
        if (!this.food.count) {
          Vue.set(this.food, 'count', 1);
        } else {
          this.food.count++;
        }
        this.$emit('add', event.target);
      },
      decreaseCart(event) {
        if (!event._constructed) {
          return;
        }
        if (this.food.count) {
          this.food.count--;
        }
      }
    }
  };
</script>

<style rel="stylesheet" lang="less" scoped>
  @r:50rem;
  .cartcontrol{
    font-size: 0;
    .cart-decrease{
      display: inline-block;
      padding: 12/@r;
      opacity: 1;
      transform: translate3d(0, 0, 0);
      .inner{
        display: inline-block;
        line-height: 48/@r;
        font-size: 48/@r;
        color: rgb(0, 160, 220);
        transition: all 0.4s linear;
        transform: rotate(0);
      }
      &.move-enter-active,
      &.move-leave-active{
        transition: all 0.4s linear;
      }
      &.move-enter,
      &.move-leave-active{
        opacity: 0;
        transform: translate3d(24px, 0, 0);
        .inner{
          transform: rotate(180deg);
        }
      } 
    }
    .cart-count{
      display: inline-block;
      vertical-align: top;
      width: 24/@r;
      padding-top: 12/@r;
      line-height: 48/@r;
      text-align: center;
      font-size: 20/@r;
      color: rgb(147, 153, 159);
    }
    .cart-add{
      display: inline-block;
      padding: 12/@r;
      line-height: 48/@r;
      font-size: 48/@r;
      color: rgb(0, 160, 220);
    }
  }
</style>


